import {adaptiveDivArr} from './adaptiveBlock.js'
import { rangesClass } from './range.js';

const burgerMenu = document.getElementById('burger-menu');

window.addEventListener('resize', () => {
    if(adaptiveDivArr.length > 0) {
        adaptiveDivArr.forEach(adapt => {
            adapt.resize();
        });
    } 

    if(window.innerWidth > 1025 && burgerMenu.classList.contains('show')) {
        burgerMenu.classList.remove('show')        
        document.querySelector('.modal-link-active')?.classList.remove('modal-link-active')
        document.body.classList.remove('lock');
        document.body.removeAttribute('style')
    }
    if(rangesClass && rangesClass.length > 0) {
        rangesClass.forEach(range => {
            range.updateSlider()
        })
    }
})