import { getScrollBarWidth } from "./utils/getScrollBarWidth.js";

const modals = document.querySelectorAll('.modal');
const modalLinks = document.querySelectorAll('a[href*="#modal/"]');
const header = document.querySelector('header');

if(modals.length > 0) {
    modals.forEach(modal => {
        const links = Array.from(modalLinks).filter(link => modal.id === link.hash.replace('#modal/', ''));

        modal.addEventListener('click', (e) => {
            const target = e.target;
            if(target.closest('.modal-close') || target.classList.contains('modal')) {
                modal.classList.remove('show')
                setTimeout(() => {
                    modal.classList.add('loading');
                }, 300)
                modal.removeAttribute('style');
                document.body.classList.remove('lock');
                document.body.removeAttribute('style');
                document.querySelector(`.modal-link-active`)?.classList.remove('modal-link-active')
                header.classList.remove('modal-open');

            }  
        })
        if(!links && links.length == 0) return;
        links.forEach(link => {

            link.addEventListener('click', (e) => {
                e.preventDefault();
                if(document.querySelector('.modal.show') !== modal) {
                    document.querySelector('.modal.show')?.classList.add('loading');
                    document.querySelector('.modal.show')?.classList.remove('show');
                    document.querySelector(`.modal-link-active`)?.classList.remove('modal-link-active');
                    
                }
    
                modal.classList.toggle('show');
                link.classList.toggle('modal-link-active');
                if(modal.classList.contains('show')){
                    window.history.pushState(
                        "", 
                        document.title, 
                        window.location.href.replace(/\#(.+)/, '').replace(/http(s?)\:\/\/([^\/]+)/, '')
                    )
    
                    document.body.classList.add('lock');
                    document.body.style.paddingRight = getScrollBarWidth() + 'px';
                    modal.style.paddingRight = getScrollBarWidth() + 'px';
                    header.classList.add('modal-open');

                    if(header.getBoundingClientRect().top < -120) {
                        modal.classList.add('no-header');
                    } else {
                        modal.classList.remove('no-header');
                    }
    
                } else {
                    document.body.classList.remove('lock');
                    document.body.removeAttribute('style');
                    modal.removeAttribute('style');
                    header.classList.remove('modal-open');
                    setTimeout(() => {
                        modal.classList.add('loading');
                    }, 300)
                }
            })
        })
    })
}

export const modalClose = () => {
    modals.forEach(modal => {
        modal.classList.remove('show');
        setTimeout(() => {
            modal.classList.add('loading');
        }, 300)
        modal.removeAttribute('style');
        document.body.classList.remove('lock');
        document.body.removeAttribute('style');
        document.querySelector(`.modal-link-active`)?.classList.remove('modal-link-active');
        header.classList.remove('modal-open');
    })
}

const openModalBtnFixed = () => {
    const link = document.querySelector('.btn-fixed');
    if(!link) return;
    link.style.transform = `translateX(-${getScrollBarWidth()}px)`
}

const closeModalBtnFixed = () => {
    const link = document.querySelector('.btn-fixed');
    if(!link) return;
    link.removeAttribute('style')
}
document.addEventListener('keyup', (e) => {
    if(e.key === 'Escape') {
        modalClose()
    }
})

