
class Range {
  constructor(range) {
    this.range = range;
    this.slider = range.querySelector(".customrange");
    this.inputText = range.querySelector('.range-value');
    this.thumb = range.querySelector(".range-sliderthumb");
    this.sliderValue = range.querySelector(".slidervalue");
    this.notify = range.querySelector('.notify');
    this.buttonCheck = range.querySelector('.range-button input');
    this.min = Number(this.slider.getAttribute("min"));
    this.max = Number(this.slider.getAttribute("max"));
    this.arrayStep = this.slider.dataset.array?.split(',');
    this.value = this.slider.dataset.start ? Number(this.slider.dataset.start) : 0;
    this._init()
  }
  _init(){
      this.slider.addEventListener("input", (e) => this.updateSlider());
      if(this.buttonCheck) {
        this._onCheckRange(this.buttonCheck.checked)
        this.buttonCheck.addEventListener('input', (e) => this._onCheckRange(e.target.checked))  
      }

      this.updateSlider();
  }
  updateSlider(){
    this.value = this.slider.value;
    let pc = (((this.value-this.min)/(this.max-this.min))) * 100
    this._setThumb(pc);
    this._setValueInput(this.value)
    this._validationInputText(this.value);
  }

  _validationInputText(val) {
    if(val < this.min) {
      this.notify.classList.add('show')
    } else {
      this.notify.classList.remove('show')
    }
  }

  _setThumb(val){
    let newx = `calc(${(this.slider.offsetWidth-55) * val/100}px - ${0}px)`;
    console.log(newx)
    this.thumb.style.left = newx;
    let max = 100;
    let degrees = 360 * (val/max);
    let rotation = `rotate(${degrees}deg) translateY(-50%)`;
    this.thumb.style.transform = rotation;
  }
  
  _setValueInput(val) {
    if(!this.inputText) return;
    this.slider.setAttribute('data-value', this.arrayStep[val]) 
    this.inputText.innerHTML = Number(this.arrayStep[val]);
  }

  _onCheckRange(checked) {
    if(checked) {
      this.buttonCheck.parentElement?.classList.add('active');
      this.range.classList.remove('default')
    } else {
      this.range.classList.add('default')
      this.buttonCheck.parentElement?.classList.remove('active');
    }
  }
}

export const rangesClass = [];
window.onload = () => {

  const ranges = document.querySelectorAll('.range');
  if(ranges.length > 0) {
    ranges.forEach(range => {
      rangesClass.push(new Range(range))
    })
  }
}


